.marker {
  margin: 0px;
  min-width: 69px;
  margin-right: 39px;
}
.modal {
  display: none;
  position: fixed;
  top: 75px;
  left: 50%;
  transform: translate(-50%, 0);
  font-family: 'Roboto';
  background: getColor('basic-color');
  font-weight: 500;
  font-size: 12px;
  line-height: 1.34;
  flex-direction: column;
  width: 280px;
  height: auto;
  max-height: 100%;
  margin: auto;
  z-index: 2;
  padding: 48px 20px 41px 20px;
  overflow: auto;

  &__backdrop {
    display: none;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: getColor('bgd-dark');
    opacity: 0.25;
    z-index: 1;
  }

  &__image {
    width: 240px;
    height: 357px;
    padding-bottom: 30px;
  }
  &__title {
    font-size: 20px;
    text-transform: uppercase;
    padding-bottom: 20px;
    line-height: 1.15;
  }

  &__info {
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding-bottom: 20px;
    padding-left: 0px;
    margin: 0px;
  }
  &__info-details {
    display: flex;
    flex-direction: row;
  }
  &__info-rate {
    display: flex;
  }
  &__info-popularity {
    display: flex;
    flex-direction: row;
  }
  &__info-title {
    display: flex;
    flex-direction: row;
  }
  &__info-gene {
    display: flex;
    flex-direction: row;
  }
  &__about-title {
    text-transform: uppercase;
  }
  &__about-text {
    text-transform: none;
    padding-top: 10px;
    padding-bottom: 21px;
    margin: 0px;
    line-height: 1.67;
  }
  &__id {
    display: none;
  }
  &__buttons {
    display: flex;
    flex-direction: row;
    gap: 15px;
  }
  &__close {
    position: absolute;
    top: 10px;
    right: 10px;
    margin: 0px;
    padding: 7px 0 6px 0;

    height: 30px;
    width: 30px;
    border-radius: 50%;
    border: 1px solid rgba(0, 0, 0, 0.1);
    background: getColor('basic-color');

    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

    cursor: pointer;
  }

  &__close:hover,
  &__close:focus {
    background: getColor('underline-color');
    color: getColor('basic-color');
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
      0px 2px 1px rgba(0, 0, 0, 0.2);
    transform: scale(1.1);
  }
}
.rate {
  display: flex;
  text-align: center;
  color: getColor('basic-color');
  background: getColor('button-color');
  border-radius: 5px;
  padding: 1px 10px 1px 10px;
  line-height: 1.17;
  &__total {
    padding: 1px 10px 1px 10px;
    line-height: 1.17;
    border-radius: 5px;
    background: getColor('basic-lighter');
  }
}
.watched-btn {
  padding: 0px;
  width: 113px;
  height: 44px;
  text-transform: uppercase;
  background: getColor('button-color');
  color: getColor('basic-color');
  border-radius: 5px;
  border: 0px;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
}

.watched-btn:hover,
.watched-btn:focus {
  transform: scale(1.1);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
}

.queue-btn {
  background: none;
  padding: 0px;
  width: 113px;
  height: 44px;
  text-transform: uppercase;
  border: 1px solid getColor('bgd-dark');
  border-radius: 5px;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
}

.queue-btn:hover,
.queue-btn:focus {
  transform: scale(1.1);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
  .modal {
    width: 618px;
    flex-direction: row;
    padding: 42px 30px 40px 28px;

    &__image {
      width: 264px;
      height: 374px;
      padding: 0px;
      padding-right: 32px;
    }

    &__about-title {
      max-width: 264px;
    }
  }
}

@media screen and (min-width: 1024px) {
  .marker {
    margin-right: 83px;
  }
  .modal {
    width: 822px;
    padding: 50px 30px 40px 30px;
    flex-direction: row;
    &__title {
      font-size: 30px;
      line-height: 1.17;
    }
    &__image {
      width: 396px;
      height: 478px;
      padding: 0px;
      padding-right: 30px;
    }
    &__about-title {
      max-width: 396px;
    }
  }
}
@media screen and (min-width: 320px) and (max-width: 767px) {
  .modal {
    max-height: 85vh;
  }
}
